.account {
  padding: 40px 0 100px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    margin-top: 20px;
  }
  &__sidebar {
    width: 450px;
    background: #fff;
    border-radius: 10px;
    padding: 24px;
  }
  &__content {
    width: calc(100% - 480px);
    background: #fff;
    border-radius: 10px;
    padding: 24px;
  }
  &__tabs {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
  }
  &__tab {
    height: 40px;
    display: flex;
    align-items: center;
    gap: 10px;
    background: #F2FCF3;
    border-radius: 6px;
    padding: 5px 15px;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: #DEF8E0;
    }
    i {
      width: 15px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
    }
  }
  .tab--active {
    background: #DEF8E0;
  }
  &__subtitle {
    font-size: 26px;
    line-height: 120%;
    font-weight: 700;
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    color: rgba(0, 0, 0, 0.54);
    margin-top: 5px;
  }
  .tab__content {
    min-height: 830px;
    display: none;
  }
  .tab__content--active {
    display: block;
  }
  .tab__content--profile {
    position: relative;
    padding-bottom: 60px;
  }
}
.orders {
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 25px;
  }
  &__item {
    background: #F9F9F9;
    border-radius: 6px;
    &-show {
      padding: 20px;
      cursor: pointer;
    }
    .item-show--active {
      .orders__item-btn {
        display: none;
      }
    }
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 16px;
    }
    &-group {
      &:nth-child(2) {
        text-align: right;
      }
    }
    &-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
      margin-top: 18px;
    }
    &-btn {
      display: flex;
      align-items: center;
      gap: 8px;
      cursor: pointer;
      span {
        font-size: 14px;
        line-height: 120%;
        font-weight: 600;
        color: #585858;
      }
      i {
        width: 18px;
        height: 18px;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #55C16D;
        border-radius: 50%;
        img {
          width: 9px;
        }
      }
    }
    &-hidden {
      position: relative;
      display: none;
      padding: 20px;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% - 40px);
        height: 3px;
        background: rgba(224, 224, 224, 0.52);
        pointer-events: none;
      }
      .orders__item-btn {
        justify-content: flex-end;
        margin-top: 40px;
        i {
          transform: rotate(-180deg);
        }
      }
    }
  }
  &__number {
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
  }
  &__status {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 12px;
    &-item {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 24px;
      border-radius: 32px;
      padding: 2px 8px;
      font-size: 14px;
      line-height: 120%;
      font-weight: 600;
      color: #fff;
    }
  }
  &__price {
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
  }
  &__price-text {
    font-size: 14px;
    line-height: 120%;
    font-weight: 600;
    color: #8B8B8B;
    margin-top: 6px;
  }
  &__time {
    position: relative;
    padding-left: 12px;
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 5px;
      height: 5px;
      background: #55C16D;
      border-radius: 50%;
    }
  }
  &__subitems {
    max-width: 820px;
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    gap: 16px 32px;
  }
  &__subitem {
    flex-grow: 1;
    &-title {
      font-size: 12px;
      line-height: 120%;
      font-weight: 600;
      color: #AFAFAF;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      margin-top: 8px;
    }
    &-phone,
    &-email {
      display: block;
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      margin-top: 8px;
      &:hover {
        color: #5CC280;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
  }
}
.order-card {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 16px;
  background: #fff;
  border-radius: 6px;
  padding: 20px;
  &__left {
    display: flex;
    align-items: stretch;
    gap: 15px;
  }
  &__img {
    width: 78px;
    height: 78px;
    img {
      width: 100%;
      height: 100%;
      border-radius: 5px;
    }
  }
  &__group {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
    gap: 8px;
    padding-top: 5px;
  }
  &__title {
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
  }
  &__weight,
  &__name {
    font-size: 14px;
    line-height: 120%;
    color: #C6C6C6;
  }
  &__right {
    .order-card__group {
      align-items: flex-end;
      text-align: right;
    }
  }
  .orders__price-text {
    color: #B1B1B1;
  }
}
.profile {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
  gap: 18px;
  margin-top: 18px;
  &__btn {
    position: absolute;
    left: 0;
    bottom: 20px;
    height: 44px;
    border-radius: 10px;
    padding: 5px 32px;
    span {
      font-size: 16px;
    }
  }
}
.profile-form {
  width: 100%;
  display: grid;
  grid-template-columns: 100%;
  gap: 18px;
  &__group {
    display: flex;
    align-items: center;
    gap: 8px;
    .input-title {
      width: 223px;
      font-size: 16px;
      line-height: 120%;
      color: rgba(35, 35, 35, 0.77);
    }
    input {
      max-width: 520px;
      width: 100%;
      height: 42px;
      background: #F9F9F9;
      border: 1px solid transparent;
      border-radius: 7px;
      padding: 0 16px;
      font-size: 16px;
      line-height: 120%;
    }
  }
}
.favourites {
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
    gap: 40px;
    margin-top: 25px;
  }
}

@media screen and (max-width: 1400px) {
  .account {
    &__wrap {
      gap: 20px;
    }
    &__sidebar {
      width: 380px;
    }
    &__content {
      width: calc(100% - 400px);
    }
  }
  .favourites {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .account {
    padding-top: 10px;
    &__wrap {
      flex-direction: column;
      gap: 10px;
      margin-top: 10px;
    }
    &__sidebar {
      width: 100%;
    }
    &__content {
      width: 100%;
    }
    &__tabs {
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      gap: 8px;
    }
    &__tab {
      flex-grow: 1;
    }
    .tab__content {
      min-height: unset;
    }
    .tab__content--profile {
      padding-bottom: 0;
    }
  }
  .orders {
    &__items {
      margin-top: 15px;
    }
  }
  .profile {
    &__btn {
      position: static;
    }
  }
  .favourites {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    }
  }
}

@media screen and (max-width: 768px) {
  .favourites {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .account {
    padding: 10px 0 60px;
    &__sidebar {
      padding: 12px;
    }
    &__tab {
      span {
        font-size: 14px;
      }
    }
    &__tab--exit {
      width: 40px;
      flex-grow: unset;
      justify-content: center;
      padding: 5px;
      span {
        display: none;
      }
    }
    &__content {
      padding: 20px 16px;
    }
  }
  .orders {
    &__item {
      &-show {
        padding: 16px;
      }
      &-top {
        flex-direction: column;
        gap: 12px;
      }
      &-group {
        &:nth-child(2) {
          text-align: start;
        }
      }
      &-bottom {
        flex-direction: column;
        align-items: start;
        gap: 32px;
        margin-top: 16px;
      }
      &-hidden {
        padding: 16px;
        .orders__item-btn {
          justify-content: start;
          margin-top: 16px;
        }
      }
    }
    &__number {
      font-size: 14px;
    }

  }
  .order-card {
    flex-direction: column;
    gap: 24px;
  }
  .profile {
    gap: 38px;
    margin-top: 38px;
  }
  .profile-form {
    gap: 38px;
    &__group {
      flex-direction: column;
      align-items: start;
      .input-title {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .favourites {
    &__cards {
     grid-template-columns: 100%;
    }
  }
}