.suppliers-main {
  padding: 40px 0 100px;
  &__title {
    margin-top: 30px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(6, calc(100% / 6 - 200px / 6));
    gap: 40px;
    margin-top: 40px;
  }
}

@media screen and (max-width: 1400px) {
  .suppliers-main {
    &__cards {
      grid-template-columns: repeat(6, calc(100% / 6 - 100px / 6));
      gap: 20px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .suppliers-main {
    &__cards {
      grid-template-columns: repeat(5, calc(20% - 80px / 5));
    }
  }
}

@media screen and (max-width: 1200px) {
  .suppliers-main {
    &__cards {
      grid-template-columns: repeat(4, calc(25% - 60px / 4));
    }
  }
}

@media screen and (max-width: 992px) {
  .suppliers-main {
    padding-top: 10px;
    &__title {
      margin-top: 10px;
    }
    &__cards {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 850px) {
  .suppliers-main {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    }
  }
}

@media screen and (max-width: 650px) {
  .suppliers-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .suppliers-main {
    padding: 10px 0 60px;
    .supplier-card {
      padding: 20px 16px;
      &__img {
        width: 80px;
        height: 80px;
      }
      &__desc {
        margin-top: 12px;
      }
      &__name {
        font-size: 14px;
      }
      &__number {
        font-size: 11px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .suppliers-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
    }
  }
}