.product-main {
  padding: 40px 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    background: #fff;
    border-radius: 10px;
    padding: 50px;
    margin-top: 30px;
  }
  &__slider {
    position: relative;
    width: 50%;
    padding-bottom: 35.1%;
  }
  &__desc {
    width: 50%;
  }
  .gallery {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: stretch;
    gap: 20px;
  }
  .gallery__small {
    width: 17%;
    height: 78%;
    order: -1;
    .swiper-slide {
      padding-bottom: 100%;
    }
  }
  .gallery__big {
    width: 83%;
    .swiper-slide {
      padding-bottom: 82%;
    }
  }
  .swiper-slide {
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  .swiper-pagination {
    display: none;
  }
  .favorite--active {
    svg path {
      fill: #FE99C5;
    }
  }
  &__title {
    margin-bottom: 10px;
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 25px;
    margin-bottom: 25px;
    &-text {
      font-size: 14px;
      line-height: 120%;
      color: #AFACAC;
    }
  }
  &__manufacturer {
    display: inline-flex;
    align-items: center;
    gap: 20px;
    background: #FAFAFA;
    border-radius: 8px;
    padding: 15px;
    margin-bottom: 15px;
    &-img {
      width: 60px;
      flex-shrink: 0;
      img {
        width: 100%;
        border-radius: 4px;
      }
    }
    &-title {
      font-size: 16px;
      line-height: 120%;
      color: #A7A7A7;
      margin-bottom: 5px;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
    }
  }
  &__price {
    font-size: 40px;
    line-height: 120%;
    font-weight: 700;
    margin-bottom: 25px;
    span {
      font-size: 25px;
      line-height: 120%;
      font-weight: 500;
      color: rgba(35, 35, 35, 0.42);
    }
  }
  &__btns {
    max-width: 535px;
    display: flex;
    align-items: stretch;
    gap: 18px;
    margin-bottom: 20px;
  }
  .cartcalc {
    width: 165px;
    height: 64px;
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    border: 1px solid rgba(0, 0, 0, 0.11);
    border-radius: 10px;
    padding: 0 8px;
    input {
      width: 100%;
      flex-grow: 1;
      text-align: center;
      background: transparent;
      border: none;
      font-size: 15px;
      line-height: 120%;
      font-weight: 500;
    }
  }
  .ccalc-minus,
  .ccalc-plus {
    width: 45px;
    height: 45px;
    flex-shrink: 0;
    border-radius: 6px;
    &:hover {
      svg path {
        fill: #232323;
      }
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__btn {
    width: 100%;
    height: 64px;
    flex-grow: 1;
    border-radius: 12px;
    padding: 5px 15px;
    span {
      font-size: 22px;
    }
  }
  &__time {
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    margin-top: 120px;
    i {
      width: 6px;
      height: 6px;
      background: #55C16D;
      box-shadow: 0px 0px 4px 0px rgb(85, 193, 109);
      border-radius: 50%;
    }
  }
  &__mob-group {
    display: none;
  }
}

@media screen and (max-width: 1500px) {
  .product-main {
    &__time {
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .product-main {
    &__wrap {
      padding: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .product-main {
    padding-top: 10px;
    &__wrap {
      flex-direction: column;
      gap: 10px;
      margin-top: 20px;
    }
    &__slider {
      width: 100%;
      padding-bottom: 0;
    }
    &__desc {
      width: 100%;
      .product-main__title,
      .product-main__info {
        display: none;
      }
    }
    .gallery {
      position: static;
      height: auto;
    }
    .gallery__big {
      width: 100%;
    }
    .gallery__small {
      display: none;
    }
    .swiper-pagination {
      left: 50%;
      bottom: 15px;
      transform: translate(-50%, 0);
      width: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #D9D9D9;
      border-radius: 32px;
      padding: 5px;
      &-bullet {
        background: #B7B7B7;
        opacity: 1 !important;
      }
      &-bullet-active {
        background: #696969;
      }
    }
    &__btn {
      span {
        font-size: 16px;
      }
    }
    &__mob-group {
      display: block;
      .product-main__info {
        margin-bottom: 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .product-main {
    padding: 10px 0 30px;
    &__wrap {
      padding: 15px;
    }
    .product-card__favorite {
      display: none;
    }
    .swiper-pagination {
      &-bullet {
        width: 5px;
        height: 5px;
      }
    }
    &__manufacturer {
      width: 100%;
    }
    &__btns {
      gap: 8px;
    }
  }
}

@media screen and (max-width: 359px) {
  .product-main {
    &__btns {
      flex-direction: column;
    }
    .cartcalc {
      width: 100%;
    }
    &__btn {
      width: 100%;
    }
  }
}
