.wrapper {
  overflow: hidden;
}
.btn-white {
  background: #fff;
  border: 1px solid #55C16D;
  color: #55C16D;
  &:hover {
    background: #55C16D;
    border-color: #fff;
    color: #fff;
  }
}
.btn-green {
  background: #5CC280;
  border: 1px solid #5CC280;
  color: #fff;
  &:hover {
    background: #fff;
    color: #5CC280;
  }
  span {
    line-height: 120%;
    font-weight: 600;
  }
}
.btn-gray {
  background: #EAEAEA;
  border: 1px solid #E2E2E2;
  color: #848484;
  &:hover {
    background: #E2E2E2;
  }
  span {
    line-height: 120%;
    font-weight: 600;
  }
}
.btn-gray__outline {
  background: transparent;
  border: 1px solid rgba(0, 0, 0, 0.09);
  color: #5E5E5E;
  &:hover {
    background: rgba(0, 0, 0, 0.09);
  }
  span {
    line-height: 120%;
    font-weight: 600;
  }
}
.h2 {
  font-size: 40px;
  line-height: 120%;
}
.section-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  &__btn {
    height: 45px;
    flex-shrink: 0;
    background: #fff;
    border-radius: 24px;
    padding: 5px 25px;
    &:hover {
      background: #55C16D;
      color: #fff;
    }
    span {
      font-size: 16px;
      line-height: 120%;
    }
  }
}
.ymaps-2-1-79-balloon {
  background: transparent !important;
  box-shadow: unset !important;
}
.ymaps-2-1-79-balloon__layout {
  background: transparent !important;
  ymaps {
    height: auto !important;
  }
}
.ymaps-2-1-79-balloon__content {
  background: transparent !important;
  border-radius: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
}
.ymaps-2-1-79-balloon__close {
  display: none;
}
.balloon {
  min-width: 153px;
  display: flex;
  align-items: center;
  gap: 8px;
  background: #55C16D;
  border-radius: 300px;
  padding: 5px 7px;
  font-family: "TildaSans";
  color: #fff;
  &__icon {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #92E49A;
    border-radius: 50%;
    img {
      width: 24px;
    }
  }
  &__title {
    font-size: 14px;
    line-height: 130%;
    font-weight: 800;
  }
  &__text {
    font-size: 10px;
    line-height: 130%;
    font-weight: 500;
  }
}
.viber {
  background: #7F4DA0;
}
.telegram {
  background: linear-gradient(203.32deg, rgb(55, 174, 226) 21.452%,rgb(30, 150, 200) 70.215%);
}
.whatsapp {
  background: #25D366;
}
.viber,
.telegram,
.whatsapp {
  &:hover {
    opacity: 0.7;
  }
}
.breadcrumbs {
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 15px;
  }
  &__item {
    position: relative;
    font-size: 14px;
    line-height: 120%;
    color: rgba(35, 35, 35, 0.55);
    &--active {
      color: #232323;
    }
    &:hover {
      color: #232323;
    }
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -10px;
        transform: translate(0, -50%);
        width: 4px;
        height: 1px;
        background: #232323;
      }
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  width: 41px;
  height: 41px;
  background: #fff;
  border-radius: 50%;
  &:after {
    display: none;
  }
  svg {
    max-width: 70%;
    height: auto;
  }
}
.swiper-button-disabled {
  opacity: 0.5 !important;
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(21.8px);
  padding: 0 15px;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 100%;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 100px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    background: transparent;
    padding: 0;
  }
}
.modal-form {
  .modal__dialog {
    max-width: 707px;
    background: #ffffff;
    border-radius: 16px;
    padding: 50px;
  }
  .modal__close {
    position: absolute;
    top: 37px;
    right: 37px;
    width: 15px;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    color: #737373;
    margin-top: 24px;
    span {
      display: inline-block;
    }
  }
  &__wrap {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 24px;
  }
  &__group {
    input {
      width: 100%;
      height: 85px;
      background: #F6F6F6;
      border: 1px solid transparent;
      border-radius: 76px;
      padding: 0 30px;
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      color: #232323;
      &::placeholder {
        color: #9E9E9E;
      }
    }
  }
  .confirmation-code {
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 16px;
    input {
      width: 74px;
      text-align: center;
      border-radius: 10px;
      padding: 0 5px;
      font-size: 24px;
    }
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }
  }
  .confirmation-code__text {
    text-align: center;
    margin-top: 0;
  }
  &__btn,
  &__btn2 {
    height: 85px;
    border-radius: 76px;
    padding: 5px 15px;
    span {
      font-size: 18px;
    }
  }
  &__check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label {
      &:before {
        background-image: url("../img/check-icon.svg");
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 34px;
      font-size: 12px;
      line-height: 120%;
      font-weight: 700;
      color: #A9A9A9;
      &:before {
        content: '';
        position: absolute;
        top: -4px;
        left: 0;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: #EFEFEF;
        border-radius: 3px;
      }
    }
  }
}
.modal-feedback {
  .modal__dialog {
    max-width: 1715px;
    background: #fff;
    border-radius: 30px;
    padding: 60px 80px;
  }
  .modal__close {
    position: absolute;
    top: 32px;
    right: 40px;
    width: 15px;
    svg {
      width: 100%;
      height: auto;
    }
  }
  .simple-rating {
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    gap: 5px 15px;
    margin-top: 15px;
    &__title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
      color: #606060;
    }
    &__items {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      gap: 5px;
      overflow: hidden;
    }
    &__item {
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 0;
      opacity: 0;
      visibility: hidden;
    }
    &__item:checked,
    &__item:checked ~ .simple-rating__label {
      svg path {
        fill: #FFE065;
      }
    }
    &__label {
      cursor: pointer;
      width: 20px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    &__label:hover,
    &__label:hover ~ .simple-rating__label,
    &__label:checked ~ .simple-rating__label:hover {
      svg path {
        fill: #FFE065;
      }
    }
  }
  &__form {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 64px / 3));
    gap: 24px 32px;
    margin-top: 45px;
    &-group {
      input {
        width: 100%;
        height: 66px;
        background: transparent;
        border: 1px solid rgba(0, 0, 0, 0.14);
        border-radius: 10px;
        padding: 0 30px;
        font-size: 14px;
        line-height: 120%;
        color: #232323;
      }
      textarea {
        width: 100%;
        height: 245px;
        background: transparent;
        border: 1px solid rgba(0, 0, 0, 0.14);
        border-radius: 10px;
        padding: 30px;
        font-size: 14px;
        line-height: 120%;
        color: #232323;
        resize: none;
      }
    }
    .form-group--big {
      grid-column: 1/4;
    }
    &-btn {
      width: 100%;
      height: 83px;
      border-radius: 48px;
      padding: 5px 15px;
      margin-top: 10px;
      span {
        font-size: 24px;
      }
    }
  }
}
.basket {
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(21.8px);
  padding-top: 50px;
  padding-left: max(30px, calc((100% - 1715px) / 2));
  padding-right: max(30px, calc((100% - 1715px) / 2));
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  &__inner {
    max-width: 960px;
    height: 100%;
    background: #fff;
    padding: 50px 50px 0 50px;
    margin-left: auto;
    overflow-y: auto;
    overflow-x: hidden;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 8px;
  }
  &__title {
    margin-bottom: 25px;
  }
  &__close {
    width: 15px;
    flex-shrink: 0;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__group {
    &:not(:first-child) {
      margin-top: 50px;
    }
  }
  &__time {
    position: relative;
    padding-left: 12px;
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 5px;
      height: 5px;
      background: #5CC280;
      box-shadow: 0 0 4px 0 #55c16d;
      border-radius: 50%;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
    margin-top: 50px;
  }
  &__total {
    min-height: 152px;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 16px;
    background: #F5F5F5;
    border-radius: 5px;
    padding: 32px;
    margin-top: 50px;
    .basket-card__subgroup {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }
  &__total2 {
    width: calc(100% + 100px);
    background: #fff;
    box-shadow: 0px -13px 28.8px 0px rgba(0, 0, 0, 0.08);
    padding: 50px;
    margin-top: 50px;
    margin-left: -50px;
    &-group {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 16px;
    }
    .basket-card__subgroup {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }
  &__btn {
    width: 100%;
    height: 80px;
    border-radius: 78px;
    padding: 5px 15px;
    margin-top: 25px;
    span {
      font-size: 18px;
    }
  }
}
.basket-card {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 16px;
  &__left {
    display: flex;
    align-items: stretch;
    gap: 32px;
  }
  &__img {
    width: 178px;
    flex-shrink: 0;
    img {
      width: 100%;
      border-radius: 5px;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: start;
    gap: 10px;
  }
  &__title {
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    color: #747474;
    margin-top: 10px;
  }
  .cartcalc {
    width: 165px;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    border: 1px solid rgba(0, 0, 0, 0.11);
    border-radius: 10px;
    padding: 5px 24px;
    input {
      width: 100%;
      height: 18px;
      flex-grow: 1;
      text-align: center;
      background: transparent;
      border: 1px solid transparent;
      font-size: 15px;
      line-height: 120%;
      font-weight: 500;
    }
  }
  .ccalc-plus,
  .ccalc-minus {
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: transparent;
    padding: 0;
    font-size: 15px;
    line-height: 120%;
    font-weight: 500;
  }
  &__right {
    flex-shrink: 0;
    .basket-card__group {
      height: 100%;
      align-items: flex-end;
      text-align: right;
    }
  }
  &__remove {
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E9FCED;
    border-radius: 50%;
    padding: 3px;
    svg {
      max-width: 14px;
      height: auto;
    }
  }
  &__price {
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
  }
  &__weight {
    font-size: 14px;
    line-height: 120%;
    color: rgba(0, 0, 0, 0.28);
    margin-top: 8px;
  }
}
.order {
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(21.8px);
  padding-top: 50px;
  padding-left: max(30px, calc((100% - 1715px) / 2));
  padding-right: max(30px, calc((100% - 1715px) / 2));
  padding-bottom: 100px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  &__inner {
    max-height: 100%;
    background: #fff;
    border-radius: 0 0 40px 40px;
    padding: 50px 35px 245px 35px;
    overflow-y: auto;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 8px;
  }
  &__close {
    width: 15px;
    flex-shrink: 0;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 35px;
    margin-top: 35px;
  }
  &__left {
    width: calc(100% - 755px);
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
  }
  &__right {
    width: 720px;
    border: 1px solid rgba(35, 35, 35, 0.18);
    border-radius: 12px;
    padding: 30px 30px 40px 30px;
  }
  &__subtitle {
    font-size: 28px;
    line-height: 120%;
    font-weight: 600;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    border-bottom: 1px solid rgba(35, 35, 35, 0.18);
    padding-bottom: 40px;
    margin-top: 30px;
    li {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 16px;
      font-size: 16px;
      line-height: 120%;
      color: #676767;
    }
  }
  &__list2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    padding-top: 30px;
    li {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 16px;
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
    }
  }
  &__btn {
    width: 100%;
    height: 80px;
    border-radius: 78px;
    padding: 5px 15px;
    margin-top: 40px;
    span {
      font-size: 18px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 15px 10px;
    margin-top: 15px;
  }
  &__item {
    background: #F5F5F5;
    border-radius: 10px;
    padding: 10px 20px;
    &-title {
      font-size: 12px;
      line-height: 120%;
      color: rgba(35, 35, 35, 0.35);
    }
    input,
    textarea {
      width: 100%;
      resize: none;
      background: transparent;
      border: 1px solid transparent;
      font-size: 16px;
      line-height: 120%;
      margin-top: 7px;
    }
  }
  &__item--big {
    min-height: 150px;
    display: flex;
    flex-direction: column;
    grid-column: 1/3;
    textarea {
      flex-grow: 1;
    }
  }
  &__checkboxes {
    display: flex;
    align-items: stretch;
    gap: 10px;
    margin-top: 16px;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label {
      background: #EFFFF4;
      border-color: #55C16D;
    }
    label {
      width: 146px;
      height: 64px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #F8F8F8;
      border: 1px solid transparent;
      border-radius: 10px;
      padding: 5px 10px;
      font-size: 16px;
      line-height: 120%;
    }
  }
}

@media screen and (max-width: 1750px) {
  .modal-feedback {
    .modal__dialog {
      width: calc(100% - 30px);
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 1400px) {
  .order {
    &__wrap {
      gap: 20px;
    }
    &__left {
      width: calc(100% - 400px);
    }
    &__right {
      width: 380px;
    }
  }
}

