  .h2 {
    font-size: 32px;
  }
  .section-top {
    &__btn {
      height: 38px;
      padding: 5px 20px;
      span {
        font-size: 14px;
      }
    }
  }
  .basket {
    padding-left: max(15px, calc((100% - 1715px) / 2));
    padding-right: max(15px, calc((100% - 1715px) / 2));
    &__inner {
      max-width: 100%;
      padding: 30px 20px 0 20px;
    }
    &__title {
      margin-bottom: 16px;
    }
    &__group {
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
    &__cards {
      gap: 25px;
      margin-top: 25px;
    }
    &__total {
      margin-top: 25px;
    }
    &__total2 {
      width: calc(100% + 40px);
      padding: 32px;
      margin-left: -20px;
    }
    &__btn {
      height: 60px;
    }
  }
  .basket-card {
    gap: 8px;
    &__left {
      gap: 16px;
    }
    &__img {
      width: 110px;
    }
    &__group {
      gap: 5px;
    }
    &__title {
      font-size: 16px;
    }
    &__subtitle {
      font-size: 14px;
      margin-top: 5px;
    }
    .cartcalc {
      width: 90px;
      height: 45px;
      padding: 5px 15px;
    }
    &__remove {
      width: 20px;
      height: 20px;
      svg {
        max-width: 7px;
      }
    }
    &__weight {
      margin-top: 5px;
    }
  }
  .order {
    padding-left: max(15px, calc((100% - 1715px) / 2));
    padding-right: max(15px, calc((100% - 1715px) / 2));
    padding-bottom: 50px;
    &__inner {
      padding-bottom: 50px;
    }
    &__wrap {
      flex-direction: column;
      margin-top: 30px;
    }
    &__left {
      width: 100%;
      gap: 20px;
    }
    &__right {
      width: 100%;
    }
    &__subtitle {
      font-size: 18px;
    }
  }
  .modal-feedback {
    .modal__dialog {
      width: 100%;
      padding: 32px 24px;
    }
    &__form {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .order {
    &__checkboxes {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 16px / 3));
      gap: 8px;
    }
    &__checkbox {
      width: 100%;
      label {
        width: 100%;
      }
    }
  }
  .modal-feedback {
    &__form {
      grid-template-columns: 100%;
      margin-top: 20px;
      &-group {
        input {
          padding: 0 20px;
        }
        textarea {
          height: 160px;
          padding: 25px 20px;
        }
      }
      .form-group--big {
        grid-column: unset;
      }
      &-btn {
        height: 66px;
        span {
          font-size: 22px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .modal-form {
    .modal__dialog {
      padding: 20px;
    }
    .modal__close {
      top: 20px;
      right: 20px;
    }
    &__title {
      text-align: center;
    }
    &__info {
      font-size: 14px;
      margin-top: 10px;
    }
    &__wrap {
      gap: 16px;
      margin-top: 16px;
    }
    &__group {
      input {
        height: 65px;
        padding: 0 20px;
      }
    }
    .confirmation-code {
      gap: 13px;
      input {
        width: 60px;
        height: 80px;
      }
    }
    &__btn,
    &__btn2 {
      height: 65px;
    }
  }
  .modal-feedback {
    &__title {
      text-align: center;
    }
    .simple-rating {
      justify-content: space-between;
    }
    &__form {
      gap: 10px;
      &-btn {
        margin-top: 20px;
      }
    }
  }
  .basket {
    &__inner {
      padding: 20px 16px 0 16px;
    }
  }
  .order {
    &__inner {
      padding: 20px;
    }
    &__right {
      padding: 20px;
    }
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      padding: 12px 16px;
    }
    &__item--big {
      min-height: 170px;
      grid-column: unset;
    }
    &__checkbox {
      label {
        height: 48px;
        font-size: 14px;
      }
    }
    &__list {
      gap: 20px;
      margin-top: 20px;
    }
    &__btn {
      height: 56px;
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 450px) {
  .basket {
    &__total {
      flex-direction: column;
      padding: 20px;
      .basket-card__subgroup {
        align-items: start;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .modal-form {
    .confirmation-code {
      gap: 10px;
      input {
        width: 40px;
        height: 60px;
      }
    }
  }
  .basket {
    &__total {
      min-height: 120px;
    }
  }
  .basket-card {
    &__img {
      width: 80px;
    }
    &__title {
      font-size: 14px;
    }
    &__subtitle {
      font-size: 12px;
    }
    .cartcalc {
      width: 60px;
      height: 30px;
      border-radius: 5px;
      padding: 5px 8px;
      input {
        font-size: 13px;
      }
    }
    .ccalc-plus,
    .ccalc-minus {
      font-size: 13px;
    }
    &__price {
      font-size: 14px;
    }
    &__weight {
      font-size: 12px;
    }
  }
  .order {
    &__checkboxes {
      grid-template-columns: 100%;
    }
    &__list,
    &__list2 {
      li {
        font-size: 12px;
      }
    }
  }
}