.contacts {
  padding: 40px 0 100px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 90px;
    margin-top: 30px;
  }
  &__desc {
    width: 360px;
  }
  .map__wrap {
    position: relative;
    width: calc(100% - 450px);
    min-height: 366px;
    padding-bottom: 35%;
  }
  .map{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
    .ymaps-2-1-79-map ymaps {
      border-radius: 12px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    margin-top: 30px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 15px;
    &-icon {
      width: 42px;
      height: 42px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 50%;
      img {
        max-width: 16px;
      }
    }
    &-title {
      font-size: 16px;
      line-height: 150%;
      font-weight: 600;
    }
    &-text {
      font-size: 16px;
      line-height: 150%;
      font-weight: 500;
    }
    &-phone,
    &-email {
      &:hover {
        color: #5CC280;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .contacts {
    &__wrap {
      gap: 30px;
    }
    .map__wrap {
      width: calc(100% - 390px);
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    padding-top: 10px;
    &__wrap {
      margin-top: 20px;
    }
    &__desc {
      width: 300px;
    }
    .map__wrap {
      width: calc(100% - 330px);
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts {
    &__wrap {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    .map__wrap {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    padding: 10px 0 60px;
  }
}