.questions {
  padding: 40px 0 100px;
  &__title {
    margin-top: 30px;
  }
  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 20px;
    margin-top: 40px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__item {
    background: #fff;
    border-radius: 7px;
    &-show {
      min-height: 64px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      padding: 8px 20px;
      cursor: pointer;
    }
    .item-show--active {
      .questions__item-btn {
        transform: rotate(-180deg);
      }
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
    }
    &-btn {
      width: 15px;
      flex-shrink: 0;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-hidden {
      display: none;
      padding: 0 20px 20px 20px;
    }
    &-text {
      min-height: 232px;
      background: #F6F6F6;
      border-radius: 9px;
      padding: 16px 20px;
      p {
        font-size: 16px;
        line-height: 120%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .questions {
    padding-top: 10px;
    &__title {
      margin-top: 10px;
    }
    &__wrap {
      grid-template-columns: 100%;
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .questions {
    padding: 10px 0 60px;
    &__wrap {
      gap: 15px;
    }
    &__items {
      gap: 15px;
    }
    &__item {
      &-title {
        font-size: 16px;
      }
      &-text {
        p {
          font-size: 14px;
        }
      }
    }
  }
}