.info {
  padding: 40px 0 100px;
  &__title {
    margin: 30px 0 40px;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    .info__text {
      width: calc(100% - 666px);
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    p {
      font-size: 20px;
      line-height: 120%;
    }
    ol {
      display: grid;
      grid-template-columns: 100%;
      gap: 40px;
      li {
        list-style: auto;
        list-style-position: inside;
        font-size: 20px;
        line-height: 120%;
      }
    }
  }
  &__text2 {
    gap: 20px;
  }
  &__img {
    width: 636px;
    img {
      width: 100%;
      border-radius: 18px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .info {
    &__wrap {
      align-items: start;
      .info__text {
        width: calc(100% - 430px);
      }
    }
    &__img {
      width: 400px;
    }
  }
}

@media screen and (max-width: 992px) {
  .info {
    padding-top: 10px;
    &__wrap {
      flex-direction: column;
      .info__text {
        width: 100%;
      }
    }
    &__img {
      width: 100%;
      order: -1;
    }
  }
}

@media screen and (max-width: 576px) {
  .info {
    padding: 10px 0 60px;
    &__title {
      margin-top: 10px;
      margin-bottom: 30px;
    }
    &__text {
      p {
        font-size: 14px;
      }
      ol {
        li {
          font-size: 14px;
        }
      }
    }
  }
}