.supplier-main {
  padding: 40px 0 100px;
  &__title {
    margin-top: 30px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    color: #A6A6A6;
    margin-top: 15px;
  }
  &__text {
    max-width: 1465px;
    margin-top: 30px;
    p {
      font-size: 16px;
      line-height: 120%;
      color: #333333;
    }
  }
  &__cards {
    max-width: 1275px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
    gap: 40px;
    margin-top: 30px;
  }
}
.supplier-gallery {
  max-width: 1275px;
  margin-top: 50px;
  &__title {
    font-size: 32px;
    line-height: 120%;
    font-weight: 600;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
    gap: 40px;
    margin-top: 30px;
  }
  &__card {
    position: relative;
    padding-bottom: 100%;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.125);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      transition: all 0.3s;
    }
  }
}
.supplier-video {
  margin-top: 50px;
  &__title {
    font-size: 32px;
    line-height: 120%;
    font-weight: 600;
  }
  .video-main__card {
    max-width: 1275px;
    margin-top: 30px;
  }
  .video-main__card-img {
    padding-bottom: 42%;
  }
  .video-main__card-group {
    max-width: 585px;
  }
}

@media screen and (max-width: 1400px) {
  .supplier-main {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 20px;
    }
  }
  .supplier-gallery {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .supplier-main {
    padding-top: 10px;
    &__title {
      margin-top: 10px;
    }
    &__subtitle {
      font-size: 14px;
      margin-top: 10px;
    }
    &__cards {
      margin-top: 20px;
    }
  }
  .supplier-gallery {
    margin-top: 30px;
    &__cards {
      margin-top: 20px;
    }
  }
  .supplier-video {
    margin-top: 30px;
    .video-main__card {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .supplier-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  .supplier-gallery {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .supplier-main {
    padding: 10px 0 60px;
    .product-card {
      padding: 10px;
    }
    .product-card__desc {
      margin-top: 8px;
    }
    .product-card__group {
      margin-top: 8px;
    }
    .product-card__price {
      font-size: 12px;
    }
    .product-card__weight {
      font-size: 11px;
    }
    .product-card__btn {
      height: 27px;
      border-radius: 5px;
      margin-top: 8px;
      span {
        font-size: 9px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .supplier-gallery {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 359px) {
  .supplier-main {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}