.video-main {
  padding: 40px 0 150px;
  &__title {
    margin-top: 30px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 40px / 2));
    gap: 40px;
    margin-top: 30px;
  }
}
.video-main__card {
  position: relative;
  display: block;
  background: #fff;
  border-radius: 15px;
  padding: 20px;
  &-img {
    position: relative;
    padding-bottom: 51.5%;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      transform: rotate(180deg);
      width: 100%;
      height: 100%;
      background: linear-gradient(0.00deg, rgba(0, 0, 0, 0) 52.252%,rgba(0, 0, 0, 0.9) 100%),url(44251058);
      border-radius: 12px;
      pointer-events: none;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &-play {
    position: absolute;
    z-index: 3;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 75px;
    height: 75px;
    background: #fff;
    border-radius: 50%;
    img {
      width: 16px;
    }
  }
  &-desc {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 8px;
    padding: 40px;
  }
  &-group {
    padding-bottom: 20px;
  }
  &-title {
    font-size: 28px;
    line-height: 120%;
    font-weight: 600;
    color: #fff;
  }
  &-text {
    font-size: 16px;
    line-height: 120%;
    color: #fff;
    margin-top: 10px;
  }
  &-time {
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.44);
    border-radius: 10px;
    padding: 7px 20px;
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
    color: #fff;
  }
}

@media screen and (max-width: 1200px) {
  .video-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
  }
  .video-main__card {
    &-title {
      font-size: 24px;
    }
    &-text {
      font-size: 14px;
    }
    &-time {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 992px) {
  .video-main {
    padding-top: 10px;
    padding-bottom: 100px;
    &__title {
      margin-top: 10px;
    }
    &__cards {
      grid-template-columns: 100%;
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .video-main {
    padding: 10px 0 60px;
  }
  .video-main__card {
    &-img {
      min-height: 400px;
    }
    &-desc {
      flex-direction: column;
      align-items: start;
      gap: 10px;
    }
    &-group {
      padding-bottom: 0;
    }
    &-time {
      order: -1;
      font-size: 12px;
    }
  }
}