.search-results {
  padding: 40px 0 100px;
  &__title {
    margin-top: 30px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 30px;
    margin-top: 30px;
  }
}

@media screen and (max-width: 1400px) {
  .search-results {
    &__cards {
      grid-template-columns: repeat(4, calc(25% - 60px / 4));
      gap: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .search-results {
    padding-top: 10px;
    &__title {
      margin-top: 10px;
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .search-results {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .search-results {
    padding: 10px 0 60px;
  }
}

@media screen and (max-width: 450px) {
  .search-results {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}